*{
    margin: 0;
    padding: 0;
}

.App{
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column
}

#inv-input{
    opacity: 0;
    position: absolute;
}

.side{
    display: block;
}

.side.win{
    background-color: #2ecc71;
}

.dis{
    pointer-events: none;
}

.side__name{
    font-size: 29px;
    margin-bottom: 10px;
}

.side-wrapper{
    margin-top: 15px;
}

.side__wrapper{
    width: 327px;
    display: flex;
    justify-content: space-between;
}

.hero-select{
    display: inline-block;
    width: 60px;
    height: 70px;
    border: none;
    outline: none;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center top;
    cursor: pointer;
    position: relative;
    image-rendering: optimizeSpeed;             /* STOP SMOOTHING, GIVE ME SPEED  */
    image-rendering: -moz-crisp-edges;          /* Firefox                        */
    image-rendering: -o-crisp-edges;            /* Opera                          */
    image-rendering: -webkit-optimize-contrast; /* Chrome (and eventually Safari) */
    image-rendering: pixelated; /* Chrome */
    image-rendering: optimize-contrast;         /* CSS3 Proposed                  */
    -ms-interpolation-mode: nearest-neighbor;
    background-size: 80%;
}

.hero-select:hover{
    background-color: #ecf0f1;
}

.match{
    padding: 5px 15px;
    border-radius: 5px;
}

.match:hover{
    background-color: #ecf0f1;
}

.match-wrapper{
    display: flex;
    align-items: center;
}

.match-id{
    font-size: 20px;
    cursor: pointer;
    font-weight: bold;
    display: none;
}

.rank{
    width: 70px;
    height: 70px;
    background-image: url('https://gamepedia.cursecdn.com/dota2_gamepedia/a/ad/SeasonalRankTop2.png?version=89000c52fcb08e0ccafec82a886e26fc');
    margin-left: 10px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

#find{
    width: 200px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    font-size: 22px;
    background-color: #c0392b;
    color: white;
    margin: 30px auto;
    cursor: pointer;
    border-radius: 4px;
    -webkit-transition: background-color 0.25s ease-out;
    -moz-transition: background-color 0.25s ease-out;
    -o-transition: background-color 0.25s ease-out;
    transition: background-color 0.25s ease-out;
}

#find:hover{
    background-color: #af372b;
}

.logo{
    width: 366px;
    font-size: 36px;
    font-weight: 500;
    margin: 30px 0 50px 0;
}

.logo__sub{
    font-size: 18px;
    font-weight: 400;
    color: #7f8c8d;
}

.hero-select select{
    width: 60px;
    position: absolute;
    bottom: 0;
    border: none;
    outline: none;
    background: none;
}

.hero-select > select::-ms-expand{
    display: none;
}

#find.disabled{
    background-color: #ecf0f1;
    cursor: not-allowed;
}

.load__name{
    font-size: 32px;
    text-align: center;
    font-weight: 500;
}

footer{
    margin-top: 10px;
    font-size: 16px;
    color: #95a5a6;
    padding-bottom: 30px;
}

.exm{
    width: 366px;
}

.PS-sub{
    font-size: 14px;
    font-weight: bold;
}

.sideName{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: #95a5a6;
    font-size: 14px;
    margin: 10px 0 10px 0;
}

a{
    text-decoration: none;
    color: darkgray;
}